import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Car } from 'src/app/core/models/test/car';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

interface AnyData {
    data: [];
}

interface KhangData {
    khang: [];
}

@Injectable()
export class CarService {

    constructor(private http: HttpClient) {}

    getCars() {
        return this.http.get<AnyData>('assets/data/cars-data.json')
            .toPromise()
            .then(res => <Car[]> res.data)
            .then(data => data);
    }
    getCarsConfig() {
        return this.http.get<any>('assets/data/cars-formfit.json')
            .toPromise()
            .then(res => res.data)
            .then(data => data);
    }
    getCarsColumns() {
        return this.http.get<AnyData>('assets/data/cars-col.json')
            .toPromise()
            .then(res => res.data)
            .then(data => data);
    }
    getCarsColoumnGroup() {
        return this.http.get<AnyData>('assets/data/cars-colgroup.json')
            .toPromise()
            .then(res => res.data)
            .then(data => data);
    }
    getKhang() {
        return this.http.get<KhangData>('assets/data/cars-data.json')
            .toPromise()
            .then(res => res.khang)
            .then(data => data);
    }
    getKhangConfig() {
        return this.http.get<any>('assets/data/cars-formfit.json')
            .toPromise()
            .then(res => res.khang)
            .then(data => data);
    }
    getKhangColumns() {
        return this.http.get<KhangData>('assets/data/cars-col.json')
            .toPromise()
            .then(res => res.khang)
            .then(data => data);
    }
}
