import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CarService } from 'src/app/core/services/test/carservice';
import { FormFitComponent } from 'src/app/components/common/form-fit/form-fit.component';

@Component({
  selector: 'app-acc-page-danhmuc',
  templateUrl: './acc-page-danhmuc.component.html',
  styleUrls: ['./acc-page-danhmuc.component.scss'],
  providers: [CarService]
})
export class AccPageDanhmucComponent implements OnInit, OnDestroy {

  @ViewChild(FormFitComponent) formfit: FormFitComponent;
  listName: string;
  dataList: any[];
  dataListConfig: any[];
  dataListCol = [];
  dataListColGroup = [];
  isLoadDone = false;
  isCallOpen = false;

  constructor(private carService: CarService,
              private activeRoute: ActivatedRoute) { }
  ngOnInit() {
    this.activeRoute.queryParams.subscribe(data => {
      this.listName = data.danhmuc;
      this.onLoadData();
      this.isCallOpen = true;
    });
  }
  ngOnDestroy() {}

  // init data
  onLoadData() {
    this.isLoadDone = false;
    if (this.listName === 'tscd') {
      this.carService.getCars().then(data => {
        this.dataList = data;
        this.checkCompletedData('data');
      });
      this.carService.getCarsConfig().then(data => {
        this.dataListConfig = data;
        this.checkCompletedData('cfg');
      });
      this.carService.getCarsColumns().then(data => {
        this.dataListCol = data;
        this.checkCompletedData('col cfg');
      });
      this.carService.getCarsColoumnGroup().then(data => {
        this.dataListColGroup = data;
      });
    } else {
      this.isLoadDone = false;
      this.carService.getKhang().then(data => {
        this.dataList = data;
        this.checkCompletedData('data');
      });
      this.carService.getKhangConfig().then(data => {
        this.dataListConfig = data;
        this.checkCompletedData('cfg');
      });
      this.carService.getKhangColumns().then(data => {
        this.dataListCol = data;
        this.checkCompletedData('col cfg');
      });
    }
  }
  openList() {
    if (this.isLoadDone && typeof this.formfit !== 'undefined') {
      this.formfit.show();
    }

  }
  //  check download completed
  checkCompletedData(type?: string) {
    this.isLoadDone = typeof this.dataListConfig !== 'undefined' &&
      typeof this.dataList.length !== 'undefined' && this.dataList.length > 0 &&
      typeof this.dataListCol !== 'undefined' && this.dataListCol.length > 0;
    if (this.isLoadDone && this.isCallOpen) {
      this.openList();
    }
  }
}
