import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AccPagesModule } from './pages/acc/acc-pages.module';
import { AdminPagesModule } from './pages/admin/admin-pages.module';
import { CommonPagesModule } from './pages/common/common-pages.module';

const routes: Routes = [
  {path: '', loadChildren: () => AccPagesModule},
  {path: 'acc', loadChildren: () => AccPagesModule},
  {path: 'common', loadChildren: () => CommonPagesModule}

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
