import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CommonComponentsModule } from 'src/app/components/common/common.components.module';
import { PrimeNgModule } from 'src/app/core/modules/prime-ng.module';
import { Routes, RouterModule } from '@angular/router';

import { AccPageHomeComponent } from './acc-page-home/acc-page-home.component';
import { AccLayoutComponent } from './acc-layout/acc-layout.component';
import { AccPageDanhmucComponent } from './acc-page-danhmuc/acc-page-danhmuc.component';


const accRoute: Routes = [
    {path: '', component: AccLayoutComponent, children: [
        {path: '',          component: AccPageHomeComponent},
        {path: 'home',      component: AccPageHomeComponent},
        {path: 'danhmuc',   component: AccPageDanhmucComponent}
    ]}
  ];

@NgModule({
    declarations: [
        AccLayoutComponent,
        AccPageHomeComponent,
        AccPageDanhmucComponent
    ],
    imports: [
        RouterModule.forChild(accRoute),
        CommonModule,
        CommonComponentsModule,
        PrimeNgModule
    ],
    exports: [
        RouterModule
    ]
})
export class AccPagesModule { }
