import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { PrimeNgModule } from 'src/app/core/modules/prime-ng.module';

import { CommonLayoutComponent } from './common-layout/common-layout.component';
import { CommonPageNotFoundComponent } from './common-page-not-found/page-not-found.component';
import { CommonPageHomeComponent } from './common-page-home/common-page-home.component';
import { CommonPagePrintComponent } from './common-page-print/common-page-print.component';


const commonRoute: Routes = [
    {path: '', component: CommonLayoutComponent, children: [
        { path: '',         component: CommonPageHomeComponent },
        { path: 'home',     component: CommonPageHomeComponent },
        { path: 'print',    component: CommonPagePrintComponent },
        { path: '**',       component: CommonPageNotFoundComponent }
    ]}
  ];

@NgModule({
    declarations: [
        CommonLayoutComponent,
        CommonPageNotFoundComponent,
        CommonPageHomeComponent,
        CommonPagePrintComponent
    ],
    imports: [
        RouterModule.forChild(commonRoute),
        CommonModule,
        PrimeNgModule
    ],
    exports: [
        RouterModule
    ]
})
export class CommonPagesModule { }
