import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-form-dung',
  templateUrl: './form-dung.component.html',
  styleUrls: ['./form-dung.component.scss'],
  providers: []
})
export class FormDungComponent implements OnInit {
  @Input() title: string;
  @Input() rowData: any;
  @Input() colSource: any;
  public newData = false;
  public displayDialog = false;
  public command = '';
  constructor() { }

  ngOnInit() {
  }
  isValidData(curData) {
    return typeof curData === 'object';
  }
  showForm(option: boolean, command?: string) {
    this.displayDialog = option;
    this.command = command;
  }
  delete() {}
  save() {}
  cancel() {
    this.displayDialog = false;
  }
}
