import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { MenuItem } from 'primeng/api';
import { CarService } from 'src/app/core/services/test/carservice';

interface ConfigData {
  id: number;
  ten: string;
}

@Component({
  selector: 'app-common-page-print',
  templateUrl: './common-page-print.component.html',
  styleUrls: ['./common-page-print.component.scss'],
  providers: [CarService]
})

export class CommonPagePrintComponent implements OnInit {
  title = '';
  dataSource: any[];
  colSource: any[];
  configSource: ConfigData;
  table: string;
  isLoadDone = false;
  items: MenuItem[];
  items1: MenuItem[];
  sidebarStatus = false;
  constructor(private location: Location, private activeRoute: ActivatedRoute, private dataService: CarService) { }

  ngOnInit() {
    this.onCreateContextMenu();
    this.activeRoute.queryParams.subscribe(data => {
      this.table = data.data;
      this.onLoadData();
    });
  }
  onCreateContextMenu() {
    this.items = [
      {label: 'Print', icon: 'fa fa-print', command: (event) => this.toPrinter()},
      {label: 'Xuất Excel', icon: 'fa fa-file-export', command: (event) => this.exportExcel()},
      {separator: true},
      {label: 'Quay lại', icon: 'fa fa-undo', command: (event) => this.backPrePage()},
      {separator: true},
      {label: 'Bỏ qua', icon: 'fa fa-ban', command: () => this.sidebarStatus = false}
    ];
    this.items1 = [
      {label: 'In ấn', icon: 'pi pi-pw', items: [
        {label: 'Print', icon: 'pi pi-fw pi-print', command: (event) => this.toPrinter()},
      ]},
      {label: 'Kết xuất dữ liệu', icon: 'pi pi-pw', items: [
        {label: 'Xuất Excel', icon: 'pi pi-fw pi-download', command: (event) => this.exportExcel()},
        {separator: true},
        {label: 'Xuất PDF', icon: 'pi pi-fw pi-save'}
      ]},
      {label: 'Quay lại', icon: 'fa fa-undo', command: (event) => this.backPrePage()},
      {label: 'Bỏ qua', icon: 'fa fa-ban', command: () => this.sidebarStatus = false}
    ];
  }
  onLoadData() {
    this.isLoadDone = false;
    if (this.table === 'khang') {
      this.dataService.getKhang().then(data => {
        this.dataSource = data;
        this.checkLoadDone();
      });
      this.dataService.getKhangColumns().then(data => {
        this.colSource = data;
        this.checkLoadDone();
      });
      this.dataService.getKhangConfig().then(data => {
        this.configSource = data;
        this.checkLoadDone();
      });
    }
    if (this.table === 'tscd') {
      this.dataService.getCars().then(data => {
        this.dataSource = data;
        this.checkLoadDone();
      });
      this.dataService.getCarsColumns().then(data => {
        this.colSource = data;
        this.checkLoadDone();
      });
      this.dataService.getCarsConfig().then(data => {
        this.configSource = data;
        this.checkLoadDone();
      });
    }
  }
  checkLoadDone() {
    this.isLoadDone = typeof this.configSource !== 'undefined' &&
      typeof this.dataSource.length !== 'undefined' && this.dataSource.length > 0 &&
      typeof this.colSource !== 'undefined' && this.colSource.length > 0;

    if (typeof this.configSource !== 'undefined') {
      this.title = this.configSource.ten;
    }
  }
  toPrinter() {
    let printContents, win;
    printContents = document.getElementById('print-section').innerHTML;
    win = window.open('', '_blank', 'top=0,left=0,fullscreen=yes');
    win.document.open();
    win.document.write(`
      <html>
        <head>
          <title>Print page</title>
          <style>
            table, td, th {border: 1px solid #ddd; text-align: left;}
            table {border-collapse: collapse;width: 100%;font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;}
            th {
              padding: 10px;
              font-weight: bolder;
              background-color: #fafafa;}
            td {
              padding: 10px;}
            h1 {
              font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
              text-align: center;
              text-transform: uppercase;}
          </style>
        </head>
        <body onload="window.print();window.close()">${printContents}</body>
      </html>`
    );
    win.document.close();
  }
  exportExcel() {
  }
  backPrePage() {
    this.location.back();
  }
}
